
import { defineComponent, defineEmit, SetupContext } from 'vue';
import moment from 'moment';

export default defineComponent({
  props: {
    dateStr: String
  },
  setup(props: any, context: SetupContext) {
    const display = moment(props.dateStr).format('M/D(ddd)')
    return {
      display
    }
  }
})
